.problems {
  padding-top: 25px;

  background-image: linear-gradient(to bottom, #111518 545px, #ffffff 148px);
}

.problems__titel {
  margin-bottom: 25px;

  font-weight: 800;
  font-size: 24px;
  line-height: 1.3;

  letter-spacing: 0.03em;

  color: var(--white-color);
}

.problems__titel--accent {
  color: #ff6b09;
}

.problems__list {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;

  height: 488px;

  counter-reset: section;

  padding-top: 19px;
  padding-left: 30px;
  padding-right: 21px;
  padding-bottom: 19px;

  font-weight: 700;
  font-size: 14px;
  line-height: 1.42;

  letter-spacing: 0.08em;

  color: var(--title-color);
  background-color: var(--white-color);

  box-shadow: 0px 8px 26px rgba(0, 0, 0, 0.1);
}

.problems__item::before {
  counter-increment: section;
  content: '\2014'counter(section, decimal-leading-zero);
  color: var(--accent-orange-color);
}
